.project-panel {
    position: fixed;
    right: 3%;
    width: 26%;
    height: 100%;
    background-color: rgba(255, 255, 255, 65%);
    padding-left: 20px;
    padding-right: 20px;

}

.project-panel h3{
    margin-bottom: 8px;
}

.project-panel-img {
    margin-bottom: 8px;
    width: 100%;
    border: 2px solid black; 
}

.project-panel-exit {
    position: absolute;
    right: 4px;
    top: 4px;
    width: 20px;
    cursor: pointer;
}

.project-panel-tags {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    
}

.project-panel-tags div {
    padding: 3px 6px 3px 6px;
    margin-right: 10px;
    margin-bottom: 10px;
    border-radius: 8%;
    background-color: #83a79d;
    color: white;
    font-size: 1em;
}

.project-panel-repo a{
    display:block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.project-panel-link a{
    display:block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

@media screen and (max-width: 980px) {
    .project-panel {
        width: 40%;
    }
}

@media screen and (max-width: 500px) {
    .project-panel-tags div {
        font-size: 0.8em;
    }
    .project-panel h2, .project-panel h3 {
        margin-top: 5px;
        margin-bottom: 4px;
    }
    .project-panel-desc {
        font-size: 0.9em;
    }
}