.solarium {
    position: relative;
    width: 100%;
    height: 50%;
    
}

.solarium img {
    width: 30px;
}

.icon-holder {
    display: flex;
    align-items: center;
    justify-content: center;
}

.motion {
    position: absolute;
    z-index: 4;

    width: 20px;
    height: 20px;

    border-radius: 50%;

    top: calc(50% - 10px);
    left: calc(50% - 10px);
}

.motion img {
    width: 25px;
}

.circle {
    position: absolute;
    border-radius: 50%;
    border: 2px solid black;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.circle-element {
    position: absolute;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    top: calc(50% - 15px);
    left: calc(50% - 15px);
}

