.project-list {
    width: 100%;
    margin-left: 50px;
    margin-right: 50px;
}

.project-list h2 {
    text-align: center;
}

.project-list h4 {
    padding: 0;
    margin: 0;
}

.project-list-section {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.project-list-item {
    width: 155px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid rgba(183, 208, 215, 40%);
    border-radius: 10%;
    padding: 10px 10px 10px 10px;

    outline: 2px solid black;
    outline-offset: -5px;

    background-color: #dae8e5;
    cursor: pointer;
}

.project-list-item img {
    width: 100px;
    height: 75px;
    margin-bottom: 5px;
}

.project-list-item-selected {
    outline: 4px solid rgb(32, 162, 197);
}