.home-paragraph {
    margin-top: 30px;
    margin-bottom: 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.home-paragraph p {
    margin-top: 0px;
    margin-bottom: 2px;
    text-align: center;
}

.home-paragraph img {
    width: 200px;
    border-radius: 50%;
    
}

@media screen and (max-width: 980px) {
    .home-paragraph {
        margin-bottom: 10px;
    }
}

@media screen and (max-width: 400px) {
    .home-paragraph img {
        width: 50%;
        margin-right: 2px;
    }
    .home-paragraph p {
        font-size: 0.8em;
    }
}