.desktop-navbar {
    position: absolute;
    margin-top: 2%;
    margin-left: 2%;
}

.mobile-navbar {
    position:absolute;
    top: 2%;
    left: calc(5% + 20px);
    z-index: 1;
}
.mobile-navbar .navbar {
    background-color: white;
}

.navbar {
    z-index: 2;
    width: 80px;
    padding-top: 30px;
    padding-bottom: 35px;

    background-color: rgba(255, 255, 255, 65%);
    text-align: center;
}

.navbar-section {
    margin-bottom: 70px;
    display: grid;
    column-count: 1fr;
    grid-auto-rows: auto;
    row-gap: 12px;
}

.navbar-section:last-child {
    margin-bottom: 0px;
}

.navbar h3 {
    cursor: pointer;
    color: black;
    text-decoration-line: none;
    margin: 0px 0px 0px 0px;
     
}
/*NavLink returns <a>*/
.navbar a:link, .navbar a:visited, .navbar a:active {
    color: black;
    text-decoration-line: none;
}
.navbar h3:hover {
    text-decoration-line: underline;
}

.navbar img {
    width: 30px;
}

.navbar-under {
    z-index: 1;
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-color: black;
    opacity: 50%;
    left: 0;
    top: 0;
}

.navbar-toggle {
    width: 30px;
    height: 30px;
    background-color: white;
    border: 1.5px solid black;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}