.page {
  position:absolute;
  width: 50%;
  height: 100vh;
  left: 25%;
  background-color: rgba(255, 255, 255, 65%);


}

.page::before{
  opacity: 20%;
}

@media screen and (max-width: 980px) {
  .page {
    width: 90%;
    left:5%;
  }
}

.loading {
  width: 150px;
  position: absolute;
  left: calc(50% - 75px);
  top: calc(50% - 75px);
}