.pageside {
    position: absolute;
    width: 50px;
    height: 100%;
    left: calc(-35px - 0.75vw);
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  @media screen and (max-width: 400px) {
    .pageside {
      left: -35px;
    }
  }
  .pageside h3 {
    justify-self: start;
    margin-top: 20px;
    writing-mode: vertical-rl;
    transform: rotate(-180deg);
  }
  
  .pageside div {
    justify-self: stretch;
    flex-grow: 1;
    margin-top: 10px;
    margin-bottom: 10px;
    border-left: 4px solid black;
  }